<template>
  <div>
    <v-expansion-panels multiple v-model="panel">
      <v-expansion-panel>
        <v-expansion-panel-header class="title">
          Filters
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <Filters />
        </v-expansion-panel-content>
      </v-expansion-panel>

      <v-expansion-panel :readonly="true" style="display: flex;">
        <div style="width: 50%;">
          <v-expansion-panel-header class="title" expand-icon="">
            Hardware
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <HardwareDetails :hardwareData="hardwareData" />
          </v-expansion-panel-content>
        </div>
        <div  style="width: 50%;">
          <v-expansion-panel-header class="title" expand-icon="">
            Software / Service
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <SoftwareDetails :softwareData="softwareData" />
          </v-expansion-panel-content>
        </div>
      </v-expansion-panel>
    </v-expansion-panels>
  </div>
</template>

<script>
import axios from "axios";
import Filters from "@/components/CustomerInquiry/Filters";
import HardwareDetails from "@/components/CustomerInquiry/HardwareDetails";
import SoftwareDetails from "@/components/CustomerInquiry/SoftwareDetails";

export default {
  name: "CustomerInquiryDetails",
  components: { Filters, HardwareDetails, SoftwareDetails },
  data() {
    return {
      panel: [0, 1, 2],
      hardwareData: [],
      softwareData: [],
    };
  },

  mounted() {
    const po = this.$route.params.po;
    console.log(this.$route.params.po);
    this.getSOsByPO(po);
  },

  methods: {
    getSOsByPO: async function(po) {
      try {
        this.overlay = true;
        const url = `${process.env.VUE_APP_BASE_URL}/VAS-CustomerPortal/api/CustomerInquiry/GetPOSOs/${po}`;
        const options = {
          url,
          method: "get",
        };
        const response = await axios(options);
        this.hardwareData = response.data.filter(
          (el) => el.epType == "Hardware"
        );
        this.softwareData = response.data.filter(
          (el) => el.epType != "Hardware"
        );
        this.overlay = false;
      } catch (e) {
        this.overlay = false;
        this.snackbar = true;
        this.text = "Error!";
        return;
      }
    },
  },
};
</script>

<style scoped>
.v-expansion-panel {
  box-shadow: 0 3px 10px -2px rgb(85 85 85 / 8%),
    0 2px 20px 0 rgb(85 85 85 / 6%), 0 1px 30px 0 rgb(85 85 85 / 3%);
}
.v-expansion-panels {
  z-index: unset;
}
</style>
