<template>
  <div style="margin:40px;">
    <v-dialog v-model="dialog" persistent max-width="90%">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark small @click="reset">
          <v-icon small class="mr-2">mdi-filter-remove</v-icon>
          Clear Filters
        </v-btn>
        <v-btn
          color="primary"
          dark
          small
          v-bind="attrs"
          v-on="on"
          style="margin-left: 10px;"
        >
          <v-icon small class="mr-2">mdi-filter</v-icon>
          Filters
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5">Filters</span>
        </v-card-title>
        <v-card-text>
          <Filters ref="filters" />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary darken-2" text @click="close">
            Close
          </v-btn>
          <v-btn color="primary" text @click="search">
            Search
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-expansion-panels multiple v-model="panel" style="margin-top: 20px;">
      <v-expansion-panel :readonly="true">
        <v-expansion-panel-header class="title" expand-icon="">
          Purchases
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-data-table
            :items="items"
            :headers="headers"
            fixed-header
            height="350"
            disable-pagination
            :hide-default-footer="true"
          >
            <template v-slot:item.view="{ item }">
              <v-btn small text class="primary" @click="goToDetailsPage(item)">
                View Details
              </v-btn>
            </template>
          </v-data-table>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>

    <overlay :overlay="overlay" />
  </div>
</template>

<script>
import axios from "axios";
import Filters from "@/components/CustomerInquiry/Filters";
import overlay from "@/utils/overlay";

export default {
  name: "CustomerInquiry",
  components: { Filters, overlay },
  data() {
    return {
      panel: [0, 1],
      items: [],
      headers: [
        { text: "PO #", value: "po" },
        { text: "Delivery Terms", value: "deliveryTerms" },
        { text: "", value: "view" },
      ],
      dialog: false,
      overlay: false,
    };
  },

  mounted() {
    this.getPartnerPOs();
  },

  methods: {
    getPartnerPOs: async function() {
      try {
        this.overlay = true;
        const { partner } = this.$route.params;
        const url = `${process.env.VUE_APP_BASE_URL}/VAS-CustomerPortal/api/CustomerInquiry/GetPartnerPOs/${partner}`;
        const options = {
          url,
          method: "get",
        };
        const response = await axios(options);
        this.items = response.data;
        this.overlay = false;
      } catch (e) {
        this.overlay = false;
        this.snackbar = true;
        this.text = "Error!";
        return;
      }
    },

    getFilteredPartnerPOs: async function(data) {
      try {
        this.overlay = true;
        const { partner } = this.$route.params;
        const url = `${process.env.VUE_APP_BASE_URL}/VAS-CustomerPortal/api/CustomerInquiry/GetFilteredPartnerPOs/${partner}`;
        const options = {
          url,
          method: "put",
          data,
        };
        const response = await axios(options);
        this.items = response.data.length ? response.data : [];
        this.overlay = false;
      } catch (e) {
        this.overlay = false;
        this.snackbar = true;
        this.text = "Error!";
        return;
      }
    },

    goToDetailsPage: function(item) {
      this.$router.push({
        name: "CustomerInquiryDetails",
        params: item,
      });
    },

    reset: function() {
      this.getPartnerPOs();
    },

    close: function() {
      this.dialog = false;
      this.$refs.filters.model = {};
    },

    search: function() {
      this.getFilteredPartnerPOs(this.$refs.filters.model);
      this.close();
    },
  },
};
</script>

<style scoped>
.v-expansion-panel {
  box-shadow: 0 3px 10px -2px rgb(85 85 85 / 8%),
    0 2px 20px 0 rgb(85 85 85 / 6%), 0 1px 30px 0 rgb(85 85 85 / 3%);
}
.v-expansion-panels {
  z-index: unset;
}
</style>
