import Vue from "vue";
import Router from "vue-router";

/********** Login Page **********/
import LoginPage from "@/views/IAM/LoginPage";

/********** Customer Inquiry Module **********/
import CustomerInquiryPage from "@/views/CustomerInquiry/CustomerInquiryPage.vue";
import CustomerInquiryDetailsPage from "@/views/CustomerInquiry/CustomerInquiryDetailsPage.vue";

Vue.use(Router);

const router = new Router({
  mode: "history",
  routes: [
    {
      path: "*",
      redirect: "/",
    },
    {
      path: "/",
      redirect: "/login",
    },
    {
      path: "/login",
      name: "Login",
      component: LoginPage,
    },
    {
      path: "/customer/orders/:partner",
      name: "CustomerInquiry",
      component: CustomerInquiryPage,
    },
    {
      path: "/customer/orders/:partner/:po",
      name: "CustomerInquiryDetails",
      component: CustomerInquiryDetailsPage,
    },
  ],
});

