<template>
  <div>
    <v-sheet
      class="rounded-lg pa-8 my-12"
      height="420"
      width="400"
      style="margin:auto;"
    >
      <div style="text-align: center;">
        <span> <img src="@/assets/logo.png" /> </span>
        <span style="display:block;" class="title">
          Welcome to VAS Logistics Portal
        </span>
      </div>

      <v-divider class="mx-0" style="margin-top:10px;"> </v-divider>

      <div style="margin-top:25px;">
        <v-text-field label="Username" dense outlined v-model="username">
        </v-text-field>
        <v-text-field
          label="Password"
          dense
          outlined
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          :type="show ? 'text' : 'password'"
          @click:append="show = !show"
          v-model="password"
        >
        </v-text-field>
        <v-btn color="primary" width="100%" @click="login">LOG IN</v-btn>
      </div>
    </v-sheet>

    <overlay :overlay="overlay" />
    <v-snackbar v-model="snackbar" :timeout="timeout" top>
      {{ text }}
      <template v-slot:action="{ attrs }">
        <v-btn color="primary" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
// import axios from "axios";
import overlay from "@/utils/overlay";

export default {
  name: "Login",
  components: { overlay },
  data() {
    return {
      username: null,
      password: null,
      show: false,
      overlay: false,
      snackbar: false,
      text: `Username is incorrect!`,
      timeout: 3000,
    };
  },

  methods: {
    login: async function() {
      try {
        this.$router.push({
          name: "CustomerInquiry",
          params: { partner: this.username },
        });
        this.snackbar = false;
      } catch (e) {
        this.snackbar = false;
        return;
      }
    },
  },

  mounted() {},
};
</script>

<style scoped></style>
